import React, { useState } from "react";
import { Box } from "@mui/material";
import DocumentManagement from "./DocumentManagement";
import TabBar from "app/formComponents/TabBar";
import DataTableManagement from "./DataTableManagement";

const DocumentManagementMain: React.FC = () => {
  const [selectedTab, setSelectedTab] = useState(0);

  const handleTabChange = (_event: React.SyntheticEvent, newValue: number) => {
    setSelectedTab(newValue);
  };

  const renderTabContent = () => {
    switch (selectedTab) {
      case 0:
        return <DocumentManagement />;
      case 1:
        return <DataTableManagement />;
      case 1:
        return (
          <Box
            sx={{
              width: "100%",
              height: "100%",
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
              flexDirection: "column",
            }}
          >
            <h2>Data Tables Tab</h2>
            <p>Here you can manage your data tables.</p>
          </Box>
        );
      default:
        return null;
    }
  };

  return (
    <div
      style={{
        backgroundColor: "#ffffff",
        height: "calc(100vh - 95px)",
        maxHeight: "100%",
        width: "100%",
        display: "flex",
        flexDirection: "column",
      }}
    >
      <Box sx={{ width: "100%" }}>
        <TabBar
          value={selectedTab}
          onChange={handleTabChange}
          tabs={["Document", "Data Tables"]}
        />
      </Box>
      <Box
        sx={{
          flex: 1,
          width: "100%",
          overflow: "hidden",
          mt: "2px",
        }}
      >
        {renderTabContent()}
      </Box>
    </div>
  );
};

export default DocumentManagementMain;
